import React, { useState, useEffect, useRef } from 'react';
import GlobalStyle from './styles/GlobalStyle';
import Header from './components/Header';
import Hero from './components/Hero';
import About from './components/About';
import Research from './components/Research';
import Professor from './components/Professor';
import Members from './components/Members';
import Gallery from './components/Gallery';
import Location from './components/Location';
import Footer from './components/Footer';
import ChatBot from './components/ChatBot';
import AnimatedSection from './components/AnimatedSection';

function App() {
  const aboutRef = useRef(null);
  const [isAboutInView, setIsAboutInView] = useState(false);

  useEffect(() => {
    const handleScroll = () => {
      if (aboutRef.current) {
        const { top, bottom } = aboutRef.current.getBoundingClientRect();
        // About      ()
        const inView = top < window.innerHeight && bottom > 0;
        setIsAboutInView(inView);
      }
    };

    window.addEventListener('scroll', handleScroll);
    handleScroll(); //      

    return () => {
      window.removeEventListener('scroll', handleScroll);
    };
  }, []);

  useEffect(() => {
    // isAboutInView   body  
    if (isAboutInView) {
      document.body.style.backgroundImage = `url(${process.env.PUBLIC_URL + '/images/space.jpg'})`;
      document.body.style.backgroundSize = 'cover';
      document.body.style.backgroundPosition = 'center';
      document.body.style.backgroundAttachment = 'fixed';
      document.body.style.transition = 'background-image 1s ease-in-out';
    } else {
      document.body.style.backgroundImage = 'none';
      document.body.style.transition = 'background-image 1s ease-in-out';
    }
  }, [isAboutInView]);

  return (
    <>
      <GlobalStyle />
      <Header />
      <main>
        <Hero />
        <AnimatedSection>
          <About ref={aboutRef} />
        </AnimatedSection>
        <Research />
        <Gallery />
        <Professor />
        <Members /> {/* AnimatedSection  */}
        <AnimatedSection>
          <Location />
        </AnimatedSection>
      </main>
      <Footer />
      <ChatBot />
    </>
  );
}

export default App; 